.troubleshoot{
  animation-name: dFadeIn;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}
.troubleshoot>span{
  display: flex;
  align-items: center;
  margin-left: 26px;
  font-size: 16px;
  font-weight: 500;
  color: #4a4a4a;
  margin-bottom: 22px;
}
.troubleshoot>span i{
  width: 17px;
    margin-right: 15px;
}
.troubleshoot_questions{
  border-radius: 16px;
}
.troubleshoot_question{
  position: relative;
  padding: 14px 22px;
  color: #464646;
  position: relative;
  cursor: pointer;
  border-bottom: 1.4px solid #d7d7d7;
  margin-bottom: 14px;
}
.troubleshoot_question::after{
  opacity: .8;
  content: "";
  width: 24px;
  height: 24px;
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../assets//iconos/arrow_down.svg);
  transition: transform .2s;
  position: absolute;
  top: 12px;
  right: 30px;
}
.troubleshoot_question p{
  display: none;
  font-size: 15px;
  color: #8c8c8c;
  margin-top: 8px;
  line-height: 1.6;
  padding-right: 50px;
  animation-name: fadeIn;
  animation-duration: .4s;
  animation-fill-mode: forwards;
}
.troubleshoot_questions>.troubleshoot_question.show_troubleshoot>p{
  display: block;
}

.troubleshoot_questions>.troubleshoot_question.show_troubleshoot:after{
  top: 14px;
  transform: rotateZ(-90deg);
}