.cheat_details_page {
  width: 100%;
  margin-top: 60px;
  display: flex;
  justify-content: center;
  margin-top: 100px;
}
.cheat_details_page_left {
  width: 900px;
  margin-bottom: 50px;
}
.cheat_details_box {
  position: relative;
  width: 100%;
  border-radius: 16px;
  border: 1px solid #d7d7d7;
  padding: 40px 44px;
  background: #fff;
  margin-bottom: 20px;
  box-sizing: border-box;
}
.cheat_details_box_top {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.cheat_details_box_top_fecha {
  display: flex;
  align-items: center;
  margin-left: 44px;
  font-size: 15px;
  color: #323232;
}

.cheat_details_box_top_fecha .cheat_details_box_top_fecha_icono {
  color: #404040;
  font-size: 14px;
  margin-right: 7px;
}

.cheat_details_box_title {
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 23px;
  font-weight: 500;
  color: #3c3c3c;
  margin-bottom: 36px;
}
.cheat_details_box_title span {
  word-wrap: break-word;
  overflow: hidden;
}
.cheat_details_box_title img {
  margin-right: 10px;
  width: 30px;
}

.cheat_details_box_middle {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.cheat_details_box_image {
  width: 40%;
  max-height: 400px;
  overflow: hidden;
  cursor: zoom-in;
  position: relative;
}
.cheat_details_box_image img {
  width: 100%;
  /* height: 100%; */
  transition: all 1s;
  border-radius: 10px;
}

.cheat_details_box_description {
  width: 55%;
  font-size: 15px;
  line-height: 1.6;
  color: #6e6e6e;
  height: 300px;
  overflow: hidden;
  overflow-y: scroll;
}

.cheat_details_box_bottom {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  position: relative;
}

.cheat_details_box_bottom_download_button {
  width: 200px;
  height: 50px;
  font-size: 20px;
  transition: 0.5s;
}
.cheat_details_box_bottom_videoURL {
  width: 200px;
  height: 50px;
  font-size: 20px;
  transition: 0.5s;
  margin-right: 30px;
}

.cheat_details_box_bottom_download_button:hover {
  background-color: rgba(111, 255, 118, 0.671);
  transform: scale(1.1);
}

.cheat_details_box_bottom_videoURL:hover {
  background-color: rgba(255, 111, 111, 0.671);
  transform: scale(1.1);
}

.cheat_details_box_bottom_download_button i {
  font-size: 20px;
  margin-right: 8px;
}

.cheat_details_box_bottom_videoURL i {
  font-size: 20px;
  margin-right: 8px;
}

.cheat_details_page_right {
  width: 15%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.cheat_details_page_right .cheat_list .cheat_box {
  width: 220px;
  height: 284px;
  margin: 10px;
}

.cheat_details_page_right .cheat_list {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-left: 24px;
  margin-top: -8px;
  box-sizing: border-box;
}

.cheat_details_page_right
  .cheat_list
  .cheat_box
  .cheat_box_body
  .buttons
  .download_button {
  width: 130px;
  font-size: 10px;
  background-color: white;
}
.cheat_details_page_right
  .cheat_list
  .cheat_box
  .cheat_box_body
  .buttons
  .admin-options {
  margin-left: 5px;
}
.other_cheats {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.cheat_details_page_left_troubleshoot {
  margin-top: 50px;
}
.other_cheats_title {
  justify-content: center;
  margin-bottom: 10px;
  text-transform: capitalize;
  font-size: 21px;
  font-weight: 300;
  font-family: "Rubik", sans-serif;
  padding-bottom: 5px;
  border-bottom: 1px solid black;
}

.cheat_details_box_bottom_like {
  right: 20%;
  top: 0;
  transition: 0.2s;
  display: flex;
  align-items: center;
  height: 20px;
}
.cheat_details_box_bottom_like span {
  margin-left: -20px;
}

.heart {
  width: 100px;
  height: 100px;
  background: url("https://cssanimation.rocks/images/posts/steps/heart.png")
    no-repeat;
  background-position: 0 0;
  cursor: pointer;
  transition: background-position 1s steps(28);
  transition-duration: 0s;
  transform: scale(80%);
}

.heart.is-liked {
  background-position: -2800px 0;
  transition: 1s steps(28);
}

@media (max-width: 1200px) {
  .cheat_details_page {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
  }
  .cheat_details_page_left {
    width: 95%;
    margin-top: 0;
  }
  .cheat_details_page_right {
    width: 100%;
    margin-top: 0;
  }
  .cheat_details_page_right .cheat_list {
    padding: 0;
    align-items: center;
  }
  .cheat_details_box_middle {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .cheat_details_box_image {
    width: 100%;
    max-height: 500px;
    justify-content: center;
  }
  .cheat_details_box_description {
    margin-top: 30px;
    width: 100%;
    height: auto;
  }
  .cheat_details_box_bottom {
    flex-direction: column;
    align-items: center;
  }
  .cheat_details_box_bottom_videoURL {
    margin-right: 0;
    margin-bottom: 15px;
  }
}
