.download_dialog {
}
.dialogTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dialogTitle span {
  font-size: 25px;
  font-weight: 400;
}
.download_dialog_title {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 800;
}
.download_dialog_title_unlock {
  color: var(--color_principal);
}
.stepZone {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}
.stepButton {
  display: flex;
  justify-content: left;
  width: 380px;
  height: 70px;
  margin: 5px;
  font-size: 18px;
  box-sizing: content-box;
  align-items: center;
}
.stepButton i {
  width: 20%;
  font-size: 25px;
}
.stepButton .title_name {
  padding: 5px;
  border-radius: 10px;
  background-color: var(--color_principal_claro);
}
.downloadZone {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}

.downloadButton {
  display: flex;
  justify-content: space-between;
  /* min-width: 230px;
  width: auto; */
  height: 50px;
  margin: 10px;
  font-size: 18px;
  box-sizing: content-box;
  align-items: center;
  background-color: rgb(117, 246, 255);
  transition: 1s;
}
.downloadButton:hover {
  transform: scale(0.95);
  background-color: rgb(140, 213, 255);
}
.downloadButton:disabled {
  background-color: rgb(255, 136, 136);
}

.downloadButton i {
  /* width: 15%; */
  margin-right: 10px;
  font-size: 20px;
}

.stepDone {
  background-color: rgb(136, 255, 136);
}

@media (max-width: 1200px) {
  .MuiDialog-paperWidthMd {
    width: 100vw;
  }
  .MuiDialog-paper {
    margin: 5px;
  }
  .downloadZone {
    flex-direction: column;
  }
}
